import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ButtonMU from '@material-ui/core/Button';
import '../../css/Main.css'


class Home extends Component {
  render() {
    return (

        <div className='admin-signup-section container-signup container'>
          <h3>Home</h3>
          <Link style={{textDecoration: 'none'}} to={{pathname:'/admin'}}>
            <ButtonMU className='btn-signup' variant="contained" color="primary">Admin</ButtonMU>
          </Link>
        <br/>
          <div style={{display:'inline-block', position:'relative', top:'10rem'}}>
            <div>
              <a className="github-button" href="https://github.com/obiwankenoobi/react-express-boilerplate" data-icon="octicon-star" data-show-count="true" aria-label="Star obiwankenoobi/react-express-boilerplate on GitHub">Star</a>      
            </div>
            <div>
              <a class="github-button" href="https://github.com/obiwankenoobi" aria-label="Follow @obiwankenoobi on GitHub">Follow @obiwankenoobi</a>
            </div>
          </div>
        </div> 

    );
  }
}

export default Home;



